%section {
  padding: 60px 0;

  @media screen and (min-width: 768px) {
    padding: 100px 0;
    margin: 0 auto;
  }
}

%container {
  margin: 0 auto;
  padding: 0 20px;


  @media screen and (min-width: 480px) {
    width: 480px;
    margin: 0 auto;
    padding: 0 20px;
  }

  @media screen and (min-width: 768px) {
    width: 768px;
    padding: 0 32px;
  }

  @media screen and (min-width: 1280px) {
    width: 1280px;
    padding: 0 16px;
  }
}