.subscribe {
  @extend %section;
  @extend %container;
  margin-top: -40px;
  padding-top: 77px;
  padding-bottom: 60px;

  background-repeat: no-repeat;
  background-size: 161px;
  background-position: top center;
  background-image: url('../images/subscribe/banan-3-161.png');

  @media (min-device-pixel-ratio: 2),
  (min-resolution: 192dpi),
  (min-resolution: 2dppx) {
    background-image: url('../images/subscribe/banan-3-322.png');
  }

  @media screen and (min-width: 480px) {
    margin-top: -70px;
    padding-top: 77px;
  }

  @media (min-device-pixel-ratio: 2),
  (min-resolution: 192dpi),
  (min-resolution: 2dppx) {
    background-image: url('../images/subscribe/banan-3-322.png');
  }

  @media screen and (min-width: 768px) {
    margin-top: -75px;
    padding-top: 136px;
    background-size: 281px;
    background-position: top right 32px;
    background-image: url('../images/subscribe/banan-2-281.png');
  }

  @media (min-device-pixel-ratio: 2),
  (min-resolution: 192dpi),
  (min-resolution: 2dppx) {
    background-image: url('../images/subscribe/banan-2-562.png');
  }

  @media screen and (min-width: 1280px) {
    padding-top: 60px;
    padding-bottom: 120px;
    background-position: right 15px;
    background-size: 546px;
    background-image: url('../images/subscribe/banan-1-546.png');
  }

  @media (min-device-pixel-ratio: 2),
  (min-resolution: 192dpi),
  (min-resolution: 2dppx) {
    background-image: url('../images/subscribe/banan-1-1092.png');
  }

  &__title {
    font-family: var(--main-font);
    font-size: 28px;
    line-height: calc(34 / 28);
    color: var(--main-txt-color);
    margin-top: 17px;
    margin-bottom: 20px;

    @media screen and(min-width: 768px) {
      font-size: 42px;
      line-height: calc(50 / 42);
      margin-top: 20px;
    }

    @media screen and(min-width: 1280px) {
      font-size: 42px;
      line-height: calc(50 / 42);
      margin-top: 0px;
    }
  }

  &__desc {
    font-family: var(--main-font);
    font-size: 14px;
    line-height: calc(16 / 14);
    color: var(--main-txt-color);
    margin-bottom: 40px;

    @media screen and(min-width:768px) {
      font-size: 16px;
      line-height: calc(24 / 16);
      margin-bottom: 24px;
    }
  }
}

.subscribe-form-icon {
  fill: #ff705d;
}

.subscribe-form-error {
  position: absolute;

  top: 46px;
  left: 10px;
  opacity: 0;

  font-family: var(--main-font);
  font-size: 10px;
  line-height: calc(15 / 10);
  color: #ff705d;

  transition: 400ms;
}

.subscribe-form {
  position: relative;
  animation: fade-in 1500ms ease 100ms 1 alternate both;

  @media screen and (min-width: 768px) {
    display: flex;
  }

  &__input {
    display: block;
    width: 100%;
    height: 42px;
    margin-bottom: 20px;
    border: 0.85px Solid #ffe550;
    border-radius: 30px;
    background-color: #fff;
    padding-top: 12px;
    padding-bottom: 12px;
    padding-left: 11px;
    outline: transparent;
    background-color: #fff;
    font-family: var(--main-font);
    font-size: 12px;
    line-height: calc(18 / 12);
    color: #505050;

    &:focus {
      color: #2c2c2c;
    }

    &:valid:not(:placeholder-shown) {
      border-color: #7eb101;
    }

    &:invalid:required:not(:placeholder-shown) {
      border-color: #ff705d;
    }

    &:invalid:not(:placeholder-shown)~.subscribe-form-error {
      opacity: 1;
    }

    @media screen and (min-width: 768px) {
      width: 394px;
      margin-bottom: 0;
    }
  }
}

.subscribe-button {
  display: block;
  width: 103px;
  height: 42px;
  margin-left: auto;
  padding: 11px 22px;
  font-family: var(--main-font);
  font-weight: 500;
  font-size: 13px;
  line-height: calc(20 / 13);
  color: #2c2c2c;

  border: none;
  background-color: #bbf330;
  border-radius: 30px;

  cursor: pointer;
  transition: background-color var(--animation-hv-fcs);
  transition: 400ms;

  @media screen and (min-width: 768px) {
    display: flex;
    margin-left: 22px;
  }

  &:hover,
  :focus {
    background-color: #b5ff00;
  }
}